:root {
--font-color: #E8E8E8;
--font-gradient: #33;
--bg-color: #0B0B0F;

}


* {
  font-family: 'Epilogue-Regular';
  list-style: none;
  text-decoration: none ;
}

body {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  font-family: Epilogue;
  color: var(--font-color);
  
}


/* Navbar */

nav {
  position: fixed;
  top: 0;
  z-index: 999;
  width: 100vw
}

.menu-navbar {
  width: 100%;
  background-color: #31313100;
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hamburger-btn,
.hamburger-btn-open,
.menu-navbar h1 {
  margin: 20px 50px;
}

h1{
  font-size: 0;
  background-color: transparent;
}



.logo{
  position: relative;
  z-index: 3;
}


.logo:hover {
  transform: rotate(360deg);
  cursor: pointer
}

.logotype {
  fill: var(--font-color);
  width: 90%;
  height: 90%;
  position: absolute;
  top: 20%;
  left: 10%;
  z-index: 2;
  opacity: 0;
  transition: .5s all;
}


.logo svg path:nth-child(1),
.logo svg path:nth-child(2) {
  transform: translateX(0);
  opacity: 1;
  transition: .4s all;
}


.logo:hover .svg-logo path:nth-child(1) {
  transform: translateX(-1.2rem);
  opacity: 0;
  transition: .2s transform, .5s opacity;
}

.logo:hover .svg-logo path:nth-child(2) {
  transform: translateX(1.2rem);
  opacity: 0;
  transition: .2s transform, .5s opacity;
}

.logo:hover .logotype {
  opacity: 1;
  transition: .3s all;
}


.desktop-navbar{
  display: none;
}


.items-navbar {
  font-family: Inter-Medium;
  background: linear-gradient(90deg, #8917e81c, #0643f921);
  box-shadow: 0 9px 15px 0 rgba(76, 76, 77, 0.3);
  backdrop-filter: blur(5px);
  width: 100%;
  margin: 0;
  padding: 1rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  visibility: hidden;
  transform: translate(-100vw);
  transition: .5s all;
  z-index: -6;
  position: absolute;
  gap: 0;
  padding-top: 0;
}
.items-navbar li {
  cursor: pointer;
  border-radius: 20px;
  padding: 1rem;
  transition: .2s all;
  width: 100%;
  text-align: center; 
}
.items-navbar li:hover {
  background-color: #31313186;
}
.items-navbar li:hover a{
  color: rgb(78, 131, 230);
  transition: .3s all;
}
.items-navbar li a {
  color: var(--font-color)
}


.navbar-shown {
  transform: translate(0);
  visibility: visible;
  transition: .5s all;
  z-index: 10;
  position: absolute;
}

.hamburger-btn,
.hamburger-btn-open {
  height: 50%;
  max-width: 10%;
  padding: 5px;
  background-color: transparent;
}

.hamburger-btn:hover,
.hamburger-btn-open {
  cursor: pointer
}

.hamburger-btn > *,
.hamburger-btn-open > * {
  width: 25px;
  height: 3px;
  background-color: #E8E8E8;
  margin: 6px 0;
  transition: 0.4s;
}

.hamburger-btn-open .bar-1 {
  transform: rotate(45deg) translate(4px, 5px);
}

.hamburger-btn-open .bar-2 {
  opacity: 0
}

.hamburger-btn-open .bar-3 {
  transform: rotate(-45deg) translate(8px, -8px);
}

.special-btn {
  font-family: Inter-Bold;
  color: var(--font-color);
  background: linear-gradient(225deg, #18C8FF 14.89%, #933FFE 85.85%);
  background-color: red;
  border-radius: 12px;
  border: none;
  margin: 1rem 0;
  padding: 13px;
  cursor: pointer;
  transition: .3s all;
}

.special-btn:hover {
  background: linear-gradient(225deg, #13a2ce 14.89%, #7231c9 85.85%);
  transform: scale(0.9);
  transition: .3s all;
}

/* Header */

.header {
  width: 100%;
  height: 100vh;
  background-image: url("./img/assets/hero-header.png");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover; 
  display: flex;
  flex-direction: column; 
  align-items: space-between;
  justify-content: space-around; 
  
}

.header-contents {
  margin: 10vh auto;
  width: 80%;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.header-title h2{
  font-family: Syne-Extra-Bold;
  font-size: 4rem;
  margin: 0;
}

.header-title p {
  font-family: Syne-Regular;
  opacity: .8;
  margin: 0;
}

.revolution{
  padding-top: 15px;
  font-family: Syne-Bold !important;
  color: white;
  font-size: 1.4rem;
}

.statue {
  background-image: url("./img/assets/Statue.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  min-width: 25rem;
  min-height: 25rem;
  animation: statue 4s ease-in-out infinite;
}

@keyframes statue {
  0%{
    transform: translateY(-10px);
  }

  50% {
    transform: translateY(0);
  }

  100%{
    transform: translateY(-10px);
  }
}


/* Scrolldown btn */
.scrollcontainer {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pointer {
  width: 4rem;
}

.scrolldown svg path{
  stroke: var(--font-color);
  animation: scrolldown 2s infinite;
}

@keyframes scrolldown
{
0% {opacity:0}
40% {opacity:1}
80% {opacity:0}
100% {opacity:0}
}

.scrolldown svg path.path1 {
  animation-delay: 0.4s;
}
.scrolldown svg path.path2 {
  animation-delay: 0.2s;
}




/*FAQs*/

.FAQ .answer{
  display: block;
  transform: translateY(-10px);
  opacity: 0;
  max-height: 0;
  transition: opacity 400ms linear, max-height 400ms linear, transform 400ms linear;
  will-change: opacity, max-height, transform;
  padding: 0;
}

.FAQ.opened .answer{
  display: block;
  transform: translateY(0px);
  opacity: 1;
  max-height: 200px;
  transition: opacity 400ms linear, max-height 400ms linear, transform 400ms linear;
  will-change: opacity, max-height, transform;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.2);
}

.multipleFAQs{
  text-align: center;
  max-width: 70vw;
  margin: 0 auto;
  border-radius: 30px;
  outline: 4px solid #BEBFFF;
  outline-offset: -4px;
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(5px);
  margin-bottom: 20vh;
}

.opened .question{
  padding: 15px;
  border-bottom: 3px solid #bebfff7a!important; 
  border-top: 3px solid #cbccff; 
  border-right: none!important;
  border-left: none!important;
}

.question:hover{
  background-color: rgba(255, 255, 255, 0.2);
  transition: background-color 200ms;
}

.question{
  padding: 15px;
  border-top: 3px solid #cbccff;
  cursor: pointer;
}

.answer{
  padding: 10px;
}


.multipleFAQs + .opened {
  border: 3px solid #bebfff7a;
}

